<template>
    <div>
        <h1>所需物资</h1>
        <el-form :model="DemandMaterials" ref="DemandMaterials" label-width="100px" class="demo-dynamic">
            
            <el-form-item v-for="(materials, index) in DemandMaterials.material" :label="'物资' + index" :key="materials.key"
                :prop="'material.' + index + '.name'+'num'" >
                <el-input v-model="materials.materials" style="width: 40%;"></el-input>
                <el-input-number style="margin-left: 10px;" v-model="materials.num" :min="1" label="123"></el-input-number>
                <!-- <el-input v-model="material.unit"  style="width: 10%;margin-left: 10px;" placeholder="单位"></el-input> -->
                <el-button style="margin-left: 10px;" @click.prevent="removeMaterial(materials)">删除</el-button>
            </el-form-item>

            <el-form-item label="接收地址">
                <el-input v-model="DemandMaterials.receivingAddress" style="width: 70%;" placeholder="请输入物资接收地址"></el-input>
            </el-form-item>

            <el-form-item label="联系电话">
                <el-input v-model="DemandMaterials.phoneNum" style="width: 70%;" placeholder="请输入联系电话"></el-input>
            </el-form-item>

            <el-form-item label="请求说明">
                <el-input type="other" style="width: 70%;" placeholder="请输入内容" v-model="DemandMaterials.other" maxlength="50" show-word-limit>
                </el-input>
            </el-form-item>

            

            <el-form-item>
                <el-button type="primary" @click="submitForm('dynamicValidateForm')">提交</el-button>
                <el-button @click="addMaterial">新增</el-button>
                <el-button @click="resetForm('dynamicValidateForm')">重置</el-button>
            </el-form-item>
        </el-form>

        
    </div>
</template>

<script>
    import axios from 'axios'
    
    export default {
        name:'DemandMaterials',
        data() {
            return {

                DemandMaterials:{
                    material:[{
                        materials:'',
                        num:0
                    }],
                    personNo:'',
                    receivingAddress:'',
                    phoneNum:'',
                    other:''
                }
                
            };
        },
        
        methods: {
            submitForm() {
                var params = new URLSearchParams();
                var arr = new Array();

                for(var i = 0,len = this.DemandMaterials.material.length; i < len;i++){
                    var DemandMaterial = {
                        
                        materials: this.DemandMaterials.material[i].materials,
                        num: this.DemandMaterials.material[i].num,
                        personNo: this.DemandMaterials.personNo,
                        receivingAddress: this.DemandMaterials.receivingAddress,
                        phoneNum: this.DemandMaterials.phoneNum,
                        other: this.DemandMaterials.other
                    }
                    arr.push(DemandMaterial)
                }
                console.log(arr)
                params.append('DemandList',arr)
                
                axios({
                    method: "post",
                    url: "http://39.107.32.92:80/api/demandMaterial",
                    params,
                    data: JSON.stringify(arr), //只有这一个参数，json格式，后台解析为实体，后台可以直接用
                    dataType: 'json',
                    //contentType : 'application/x-www-form-urlencoded;charset=UTF-8',
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8'
                    }
                }).then(res => {
                    this.DemandMaterials = {
                        material: [{
                            materials: '',
                            num: 0
                        }],
                            personNo: '',
                            receivingAddress: '',
                            phoneNum: '',
                            other: ''
                    }
                    this.$message({
                        type: "success",
                        message: "发布成功",
                    });
                    console.log(res)
                })
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            removeMaterial(item) {
                var index = this.DemandMaterials.material.indexOf(item)
                if (index !== -1) {
                    this.DemandMaterials.material.splice(index, 1)
                }
            },
            addMaterial() {
                this.DemandMaterials.material.push({
                    name:'',
                    num: 1,
                    key: Date.now()
                });
                
                
            }
        },
        mounted() {
            console.log(this.$route.query.data1)
                this.DemandMaterials.personNo = this.$route.query.data1.email
        },
       
    }
</script>

