<template>
    <div>
        <el-dialog width="30%" title="登录" :visible.sync="modelShow" :show-close="true"
            :close-on-click-modal="false" :close-on-press-escape="false">
            <el-form ref="form" :model="form" size="small" label-width="100px">
                <el-row>
                    <el-form-item label="用户名：">
                        <el-input class="search-input" v-model="form.id" />
                    </el-form-item>
                    <el-form-item label="密码：">
                        <el-input type="password" class="search-input" v-model="form.pw" />
                    </el-form-item>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer" style="text-align: center;">

                <el-button type="primary" @click="Login">登录</el-button>
        
                <el-button @click="modelShow = false">取 消</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'LoginPage',

    data() {
        return {
            // 登录身份 true = 志愿者  false = 机构
            Identity: true,
            //是否显示本面板
            modelShow: false,
            //是否可以关闭
            canClose: false,
            //表单数据
            form: {
                id: '',
                pw: '',
            },
            flag: {
                isLogin: false,
                isVol: false,
                isOrg: false
            }
        
        };
    },

    mounted() {
        
    },

    methods: {
        // 登录
        Login(){
            if(this.Identity){
                this.$options.methods.VolLogin(this);
            }else{
                this.$options.methods.OrgLogin(this);
            }
        },

        // 机构登录
        OrgLogin(that) {

            //  发送axios到后台携带用户名和密码 
            axios.get('http://39.107.32.92:80/api/orgLogin', {
                
                params: { id: that.form.id, pw: that.form.pw }
            }
            ).then(
                response => {
                    console.log('请求成功了', response.data)
                    if (response.data.logRight == true) {
                        // 改变视图隐藏flag
                        that.modelShow = false;
                        that.flag.isLogin = true;
                        that.flag.isVol = false;
                        that.flag.isOrg = true;
                        // 发送登录状态
                        that.$emit('OrgLogin', that.flag.isLogin, that.flag.isOrg)
                        that.$bus.$emit('UserInformation', response.data)
                        that.$message({
                            type: "success",
                            message: "登录成功",
                        });

                    } else {
                        that.$message({
                            type: "warning",
                            message: "用户名或密码错误!"
                        });
                    }
                },
                error => {
                    console.log('请求失败了', error.message)
                }
            )


        },
        //志愿者登录
        VolLogin(that) {
            console.log(that)
            axios.get('http://39.107.32.92:80/api/volLogin', {
                params: { id: that.form.id, pw: that.form.pw }
            }
            ).then(
                response => {
                    console.log('请求成功了', response.data)
                    if (response.data.isLogRight == true) {
                        that.modelShow = false;
                        that.flag.isLogin = true;
                        that.flag.isVol = true;
                        that.flag.isOrg = false;
                        that.$emit('VolLogin', that.flag.isLogin, that.flag.isVol)
                        that.$bus.$emit('UserInformation', response.data)

                        that.$message({
                            type: "success",
                            message: "登录成功",
                        });



                    } else {
                        that.$message({
                            type: "warning",
                            message: "用户名或密码错误!"
                        });
                    }
                },
                error => {
                    console.log('请求失败了', error.message)
                }
            )


        },
    },
};
</script>

<style lang="css">
.el-dialog__header {
    text-align: center;
}
</style>