<template>
    <div>
        <el-empty v-show="this.dataMsg[0] == null" description="暂无已读消息"></el-empty>
        <el-card class="box-card"  shadow="hover" v-for="(msg,i) in dataMsg" :key = "i" style="margin-top: 20px;">
            <div slot="header" class="clearfix">
                <span><h1>发信方：{{msg.taskName}}</h1></span>
                <span style="float: right; padding: 3px 0">{{msg.sendTime}}</span>
                <!-- <el-button style="float: right; padding: 3px 0" type="text">删除</el-button> -->
            </div>
            
            <div style="padding: 0 50px;">
                <span>
                    {{msg.messageContent}}
                </span>
            </div>
         
        </el-card>
        
    </div>
</template>

<script>
export default {
    name: 'ReadPage',

    data() {
        return {
            name:'琪琪',
            dataMsg:[],
        };
    },

    mounted() {
        const that = this
        that.$axios.get('http://39.107.32.92:80/api/findAllReadMsg', {
            params: {
                'phoneNum': that.$route.query.phoneNum,
                'flag':null
            }
        }).then(
            response => {
                console.log('请求成功了', response)
                this.dataMsg = response.data
            },
            error => {
                console.log('请求失败了', error.message)
            }
        )
    },

    methods: {
        
    },
};
</script>

<style lang="css" scoped>
      .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 80vw;
    line-height: 10px;
  }
   .bottom {
       text-align: center;
    margin-top: 13px;
    line-height: 12px;
  }
  .mass{
      width: 70%;
      
  }

</style>