<template>

    <!--  -->
    <div>
        <!-- <el-aside width="200px">
            <el-row class="tac" style="height: 100%;">
                <el-col :span="12" style="height: 100%;"> -->
                    <div class="navOrg" style="height: 100%;"
                        v-on:mouseenter="isCollapse = false"
                        v-on:mouseleave="isCollapse = true">
                        <el-menu unique-opened default-active="this.$route.path" router class="el-menu-vertical-demo" @open="handleOpen" 
                           :collapse="isCollapse" @close="handleClose" background-color="#680000" text-color="#fff" active-text-color="#ffd04b" style="height: 100%;">
                        
                            <el-menu-item>
                                <i class="el-icon-user"></i>
                                <span slot="title">
                                    <el-button v-show="!flag.isLogin" @click="Login" type="text" style="width: 100%;height: 100%;">登录</el-button>
                                    <el-button v-show="flag.isLogin" @click="logout" type="text" style="width: 100%;height: 100%;color: darkorange;">注销
                                    </el-button>
                                </span>
                            </el-menu-item>
                        
                            <el-menu-item index="1" route="/HomePage">
                                <i class="el-icon-s-home"></i>
                                <span slot="title">主页</span>
                            </el-menu-item>
                        
                        
                            <el-menu-item index="2" route="/OrganizationRegister">
                                <i class="el-icon-circle-plus"></i>
                                <span slot="title">成为志愿机构</span>
                                </el-menu-item>
                        
                            <el-menu-item index="3" :disabled="!flag.isOrg" :route="{
                                                        path:'/IssueDemand',
                                                        query:{
                                                        email:this._data.data1.email
                                                        }
                                                    }">
                                <i class="el-icon-edit"></i>
                                <span slot="title">发布需求</span>
                            </el-menu-item>
                        
                            <el-menu-item index="4" :disabled="!flag.isOrg" :route="{
                                                        path:'/VolunteerList',
                                                        query:{
                                                            email:this._data.data1.email
                                                        }
                                                    }">
                                <i class="el-icon-document"></i>
                                <span slot="title">志愿者名单</span>
                                </el-menu-item>
                        
                        
                        
                            <el-menu-item :route="{
                                        path:'/DemandMaterials',
                                        query:{
                                        data1:this._data.data1
                                        }
                                        }" index="5">
                                <i class="el-icon-box"></i>
                                <span slot="title">物资需求</span>
                            </el-menu-item>
                        
                            <el-menu-item index="8" 
                            :route="{
                                path:'/SendMsg',
                                query:{
                                    data1:this._data.data1
                                    }
                                }">
                                <i class="el-icon-s-comment"></i>
                                <span slot="title">消息中心</span>
                            </el-menu-item>
                        
                            <el-menu-item>
                                <el-button @click="switchView" type="text" style="width: 100%;height: 100%;">切换到志愿者</el-button>
                            </el-menu-item>
                        
                        </el-menu>
                    </div>
                <!-- </el-col>
            </el-row>
        </el-aside> -->
        <LoginPage @OrgLogin="OrgLogin" ref="loginPage"></LoginPage>
    </div>
</template>

<script>
   import LoginPage from '../loginpage/login_page'
export default {
    name: 'OrganizationPage',

    data() {
        return {
            isCollapse: true,
            flag: {
                isLogin: false,
                isOrg: false
            },
            data1: {}
        };
    },

    mounted() {
        this.$bus.$on('UserInformation', (data) => {
            this.data1 = data
        })
    },

    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        Login() {
            this.$refs.loginPage.modelShow = true;
            this.$refs.loginPage.Identity = false
        },
        logout() {
            this.flag.isLogin = false
            this.flag.isOrg = false
        },
        // 切换到志愿机构
        switchView() {
            this.$emit('showView');
        },
        // 登录后改变登录状态
        OrgLogin(login, org) {
            this.flag.isLogin = login
            this.flag.isOrg = org
            // this.flag.isOrg = org

        }
    },
    components:{
        LoginPage
    }
};
</script>

<style lang="css" scoped>
     .el-menu-vertical-demo:not(.el-menu--collaps-) {
