<template>
    <div>
        <el-table stripe ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%"
            @selection-change="handleSelectionChange"
            >

            <el-table-column type="selection" width="55">
            </el-table-column>

            <el-table-column label="任务名称" width="120">
                <template slot-scope="scope">{{ scope.row.taskName }}</template>
            </el-table-column>

            <el-table-column prop="orgName" label="所属机构" width="120">
            </el-table-column>

            <el-table-column prop="taskType" label="任务类型" show-overflow-tooltip :filters="[
                        {text: '疫情志愿服务', value: '疫情志愿服务'}, 
                        {text: '扶贫志愿服务', value: '扶贫志愿服务'},
                        {text: '敬老志愿服务', value: '敬老志愿服务'}]" :filter-method="filterHandler">
            </el-table-column>

            
            <el-table-column prop="address" label="服务区域" show-overflow-tooltip
                :filters="[{text: '上海', value: '上海'}, {text: '大连', value: '大连'}]" :filter-method="filterHandler">
            </el-table-column>

            <el-table-column label="开始时间" width="250">
                <template slot-scope="scope">{{ scope.row.startTime }}</template>
            </el-table-column>

            <el-table-column label="结束时间" width="250">
                <template slot-scope="scope">{{ scope.row.endTime }}</template>
            </el-table-column>


            <el-table-column prop="personNum,personMaxNum" label="人数" show-overflow-tooltip>
                <template slot-scope="scope">             
                     {{scope.row.personNum}}/{{scope.row.personMaxnum}}                       
                </template>
            </el-table-column>

            

        </el-table>
        <div style="margin-top: 20px">
            <el-button @click="submit">报名</el-button>
            <el-button @click="toggleSelection()">取消选择</el-button>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    var arr = new Array();
    export default {
        name: 'TaskList',

        data() {
            return {
                tableData:[],
                multipleSelection: [],
                phoneNum:''
            };
        },

        mounted() {
             
                axios.get('http://39.107.32.92:80/api/taskList').then(
                    response => {
                        console.log('请求成功了', response)
                        this.tableData = response.data
                    },
                    error => {
                        console.log('请求失败了', error.message)
                    }
                ),
                this.phoneNum = this.$route.query.phoneNum

                    
            
        },

        methods: {
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                console.log(this.multipleSelection)
            },
            filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value;
                
                
            },
            submit(){
                    var params = new URLSearchParams();
                    
                    // var data = JSON.stringify(this._data.multipleSelection)
                    
                    
                    arr.length = 0
                    for (var j = 0, len = this.multipleSelection.length; j < len; j++) {
                        arr.push(this.multipleSelection[j].taskNo);
                    }
                    console.log(arr)
                    params.append('phoneNum', this.phoneNum)
                    params.append('taskNo', arr)


                    axios({
                        method: "post",
                        url: "http://39.107.32.92:80/api/submitEntryForm",
                        params
                    }).then(res => {
                        console.log(res)
                    })
            }
        },
    };
</script>

