<template>
    <div>
        <el-dialog title="编辑消息" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="消息内容" :label-width="formLabelWidth">
                    <el-input v-model="form.messageContent" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="选择任务" :label-width="formLabelWidth">
                    <el-select v-model="form.taskNo" placeholder="请选择活动区域">
                        <el-option 
                        v-for="NameAndNo in TaskNameAndNo" 
                        :key="NameAndNo.taskNo"
                        :label="NameAndNo.taskName" 
                        :value="NameAndNo.taskNo"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    var NowData = new Date().toLocaleDateString().split('/').map(item => { if (item < 10) { return '0' + item } else { return item } }).join('-')
export default {
    name: 'WriteMsg',

    data() {
        return {
            TaskNameAndNo:[],
            dialogFormVisible: false,
            form:{
                messageContent:'',
                taskNo:null
            },
            formLabelWidth: '120px',
            dataform:{
                orgEmail:''
            }
        };
    },

    mounted() {
       
    },

    methods: {
        chan(NameAndNo){
            this.taskName = NameAndNo.taskName
        },
        submit(){
            
            this.dialogFormVisible = false
            var params = new URLSearchParams();
            params.append('messageContent', this._data.form.messageContent)
            params.append('taskNo', this._data.form.taskNo)
            params.append('sendTime', NowData)
            this.$axios({
                method: "post",
                url: "http://39.107.32.92:80/api/sendMsg",
                crossDomain: true,
                params
            }).then(res => {
                this.form = {
                    messageContent: '',
                    taskNo: null
                }
                this.$message({
                    type: "success",
                    message: "注册成功",
                });
                console.log(res)
            })
        }
    },
};
</script>

<style lang="css" scoped>
    *{
        text-align: center;
    }

</style>