<template>
   <el-container>
       
       
    <VolunteerPage @showView="showView" v-show="viewflag.volView"></VolunteerPage>
    <OrganizationPage @showView="showView" v-show="viewflag.orgView"></OrganizationPage>
    
       

        <el-container>
            
            
            <el-container>
                <el-main>
                    <router-view></router-view>
                </el-main>
                
            </el-container>

            
        </el-container>
    </el-container>
        
   
    
</template>

<script>
    import logo from '../assets/favicon1.png'
    import VolunteerPage from '../navigationpage/volunteer_page'
    import OrganizationPage from '../navigationpage/organization_page'
    import Header from '../homepage/header'

export default {
    name: 'OverAll',

     data() {
        return {
            // 是否折叠显示
            isCollapse: true,
            flag: {
                isLogin: false,
                isVol: false,
                isOrg: false
            },
            // 显示导航
            viewflag:{
                volView:true,
                orgView:false
            },
            imgUri:logo,
            data1:{}
        };
    },
    
    methods: {

        // 展示导航
        showView(){
            this.viewflag.volView = !this.viewflag.volView;
            this.viewflag.orgView = !this.viewflag.orgView;
        },
       
        loginShow(){
            this.$bus.$emit('modelShow', true)
        },
        logout(){
            this.flag.isLogin = false
            this.flag.isOrg = false
            this.flag.isVol = false
        },
        
        OrgLogin(login,org,vol){
            this.flag.isLogin = login
            this.flag.isOrg = org
            this.flag.isVol = vol
            
        },
        VolLogin(login,org, vol) {
            this.flag.isLogin = login
            this.flag.isVol = vol
            this.flag.isOrg = org

        }
        
        
    },

    mounted() {
        this.$bus.$on('UserInformation', (data) => {
            this.data1 = data   
        })

        
    },
    components:{
        VolunteerPage,
        OrganizationPage,
        Header
    }

};
</script>

