<template>
    <div>
        <el-badge class="item">
            <el-button size="small" @click="toRead">
                已读消息
            </el-button>
        </el-badge>
        <el-badge class="item" type="primary">
            <el-button size="small" @click="toUnRead">未读消息</el-button>
        </el-badge>
        
        <router-view></router-view>
        
    </div>
</template>

<script>

export default {
    name: 'MassagePage',

    data() {
        return {
            data1:{}
        };
    },

    mounted() {
        this.data1 = this.$route.query.data1
        console.log(this.$route.query.data1)
    },

    methods: {
        toRead(){
            console.log(this.data1)
            this.$router.push({ 
                name: 'ReadPage',
                query: {
                    phoneNum: this.data1.phoneNum
                }
            });
        },
        toUnRead() {
            this.$router.push({
                name: 'UnReadPage',
                query: {
                    phoneNum: this.data1.phoneNum
                } 
            });
        }
    },
};
</script>

<style lang="css" scoped>
.item {
  margin-top: 10px;
  margin-right: 40px;
  line-height: 0;
}

</style>