<template>
    <div>
        <el-table stripe ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%"
            @selection-change="handleSelectionChange">
    
            <el-table-column label="任务名称" width="120">
                <template slot-scope="scope">{{ scope.row.taskName }}</template>
            </el-table-column>
    
            <el-table-column prop="orgName" label="所属机构" width="120">
            </el-table-column>
            <el-table-column label="开始时间" width="120">
                <template slot-scope="scope">{{ scope.row.startTime }}</template>
            </el-table-column>

            <el-table-column label="结束时间" width="120">
                <template slot-scope="scope">{{ scope.row.endTime }}</template>
            </el-table-column>
    
    
            <el-table-column label="审核状态" show-overflow-tooltip :filters="[
                            {text: '通过', value: '通过'},
                            {text: '未通过', value: '未通过'}, 
                            ]" :filter-method="filterHandler">
                            <template slot-scope="scope">{{ scope.row.isPass ===1? '通过':'未通过' }}</template>
            </el-table-column>

            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button v-show="scope.row.isPass === 1" size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">加入</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">移除</el-button>
                </template>
            </el-table-column>
    
    
    
        </el-table>
        <div style="margin-top: 20px">
            
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    
export default {
    
    name: 'RegisteredList',

        data() {
        
            
            return {
                tableData: [],
                multipleSelection: [],
                phoneNum:''
                
            };
        },
        
            
        created() {
                
            },
        mounted:function () {
            

            const that = this
            axios.get('http://39.107.32.92:80/api/entriedTable',{
                params:{
                    phoneNum: that.$route.query.phoneNum
                }
            }).then(
                response => {
                    console.log('请求成功了', response)
                    this.tableData = response.data
                },
                error => {
                    console.log('请求失败了', error.message)
                })
            
            
                
        },

        methods: {
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value;

            },
            handleEdit(index, row) {
                var that = this
                var params = new URLSearchParams();
                params.append('taskno', row.taskNo)
                params.append('phoneNum', that.$route.query.phoneNum)
                axios({
                    method: "post",
                    url: "http://39.107.32.92/api/joinTask",
                    params
                }).then(res => {
                    console.log(res)
                })
                this.tableData.splice(index, 1)
                console.log(index, row);
                
                
            },
            handleDelete(index, row) {
                var that = this
                var params = new URLSearchParams();
                params.append('taskName', row.taskName)
                params.append('phoneNum', that.$route.query.phoneNum )
                axios({
                    method: "post",
                    url: "http://39.107.32.92:80/api/removeEntriedTask",
                    params
                }).then(res => {
                    console.log(res)
                })
                this.tableData.splice(index,1)
                console.log(index, row);
                
            },
            
                
            
        },
    };
</script>

<style lang="css" scoped>

</style>