<template>
    <div>
        <el-table stripe :data="tableData1" style="width: 100%" row-key="id" >
            <el-table-column prop="orgName" label="发起人" width="180" fixed >
            </el-table-column>
            <el-table-column prop="materials" label="物资名称" width="180">
            </el-table-column>
            <el-table-column prop="nowNum" label="现有数量" width="180">
            </el-table-column>
            <el-table-column prop="num" label="需求数量" width="180">
            </el-table-column>
            <el-table-column prop="receivingAddress" label="接收地址">
            </el-table-column>
            <el-table-column prop="other" label="请求说明">
            </el-table-column>
            

            <el-table-column fixed="right" label="操作" width="200px">
                <template slot-scope="scope">
                    <el-button @click="donateTo(scope.row)" type="primary" size="mini">捐赠</el-button>
                </template>
            </el-table-column>
        </el-table>
        <DonateDilog ></DonateDilog>
    </div>
    
</template>
<script>
    import DonateDilog from './donate_dilog'
    import axios from 'axios'
    export default {
        data() {
            return {
                personNo:'',
                tableData1:[]
                
            }
        },
        methods: {
            donateTo(row){
                this.$bus.$emit('dialogVisible', true)
                this.$bus.$emit('personNo',this.personNo)
                this.$bus.$emit('donate', row.demandNo)
            },
            
        },
        components: {
            DonateDilog
            },
        mounted() {
             axios.get('http://39.107.32.92:80/api/demandList').then(
                response => {
                    console.log('请求成功了', response)
                    this.tableData1 = response.data
                },
                error => {
                    console.log('请求失败了', error.message)
                }

            )
            if ('phoneNum' in this.$route.query.data1) {
                this.personNo = this.$route.query.data1.phoneNum
            } else if ('email' in this.$route.query.data1) {
                this.personNo = this.$route.query.data1.email
            }

        },
    }
</script>