<template>
    <div>
        <el-dialog :visible.sync="dialogVisible" width="30%" >
            
            <el-form ref="form" :model="form" label-width="80px">
                
                <el-form-item label="运送快递">
                    <el-select v-model="form.expressage" placeholder="请选择运送快递">
                        <el-option label="顺丰速递" value="shunfeng"></el-option>
                        <el-option label="德邦速递" value="debang"></el-option>
                    </el-select>
                </el-form-item>
                
                <el-form-item label="匿名捐赠">
                    <el-switch v-model="form.isAnonymity"></el-switch>
                </el-form-item>
                
                
                <el-form-item label="捐赠数量">
                    <el-input-number v-model="form.sum" :min="1" :max="10" ></el-input-number>
                </el-form-item>

                <el-form-item label="运输单号">
                    <el-input v-model="form.expressageNum"></el-input>
                </el-form-item>
                
            </el-form>

            <span slot="footer" class="dialog-footer" >
                <el-button type="primary" @click="donate()">确 定</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
                
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'DonateDilog',

   data() {
            return {
                dialogVisible: false,
                form:
                    {
                        pname:'',
                        expressage:'',
                        isAnonymity:false,
                        sum:1,
                        expressageNum:'',
                        donateNo:''


                    }
                
            };
        },
    methods: {
       donate(){
            var params = new URLSearchParams();
            params.append('personName', this._data.form.pname)
            params.append('expressName', this._data.form.expressage)
            params.append('Num', this._data.form.sum)
            params.append('Number', this._data.form.expressageNum)
            params.append('donateNo', this._data.form.donateNo)
            axios({
                method: "post",
                url: "http://39.107.32.92:80/api/donateMaterial",
                params
            }).then(res => {
                this.dialogVisible = false
                console.log(res)
            })
       }
    },

    mounted: function () {
                this.$bus.$on('dialogVisible', (data) => {
                    this.dialogVisible = data
                });
                 this.$bus.$on('personNo', (data) => {
                    this.form.pname = data
                });
                 this.$bus.$on('donate', (data) => {
                    this.form.donateNo = data
                });

                
        
            }

   
};
</script>

<style lang="css">
    .el-dialog__footer{
        text-align: center;
    }

</style>