<template>
    <div>
        
        <el-table stripe ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%"
            >
            

            <el-table-column label="志愿者姓名" width="120">
                <template slot-scope="scope">{{ scope.row.memberName }}</template>
            </el-table-column>

            <el-table-column prop="taskName" label="任务名称" width="120">
            </el-table-column>


            <el-table-column prop="educationalLevel" label="学历" show-overflow-tooltip :filters="[
                                    {text: '无', value: '无'},
                                    {text: '博士', value: '博士'}, 
                                    {text: '硕士', value: '硕士'},
                                    {text: '本科', value: '本科'},
                                    {text: '高中', value: '高中'},
                                    {text: '初中', value: '初中'},
                                    {text: '小学', value: '小学'}]" :filter-method="filterHandler">
            </el-table-column>
            

            <el-table-column prop="skill" label="技能" show-overflow-tooltip >
            </el-table-column>

            <el-table-column prop="grade" label="平均分" show-overflow-tooltip>
            </el-table-column>

            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary"
                        @click="handleEdit(scope.$index, scope.row)">通过审核</el-button>
                </template>
            </el-table-column>


            



        </el-table>
        
    </div>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'VolunteerList',

    data() {
        return {
            tableData: [],
            multipleSelection: [],
            data1:{}
        };
    },
    created() {
       
        
        
        
    },

    mounted() {
        
        const that = this
        axios.get('http://39.107.32.92:80/api/SelectRegisteredMember', {
            params: {
                'email': that.$route.query.email
            }
        }).then(
            response => {
                console.log('请求成功了', response)
                this.tableData = response.data
            },
            error => {
                console.log('请求失败了', error.message)
            }
        ) 
        
        
    },

    methods: {
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        
        filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value;
                
            },
        handleEdit(index, row) {
            
            var params = new URLSearchParams();
            params.append('taskNo', row.taskNo)
            params.append('phoneNum', row.phoneNum)
            axios({
                method: "post",
                url: "http://39.107.32.92:80/api/passVerification",
                params
            }).then(res => {
                console.log(res)
            })
            this.tableData.splice(index, 1)
            console.log(index, row);
        },
       
    },
};
</script>

