<template>
    <div>
        <el-table stripe ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%"
            @selection-change="handleSelectionChange">
            

            <el-table-column label="任务名称" width="120">
                <template slot-scope="scope">{{ scope.row.taskName }}</template>
            </el-table-column>

            <el-table-column prop="volName" label="成员姓名" width="120">
            </el-table-column>

            <el-table-column prop="phoneNum" label="成员电话" show-overflow-tooltip
                >
            </el-table-column>

           <el-table-column prop="isGrade" label="评分">
                <template slot-scope="scope">
                    <div class="block" >
                        <el-rate v-model="scope.row.isGrade" @change="change(scope.row)" :colors="colors">
                        </el-rate>
                    </div>                       
                      
                </template>
           </el-table-column>



        </el-table>
       
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: 'RegisteredVolList',

        data() {
            return {
                
                
                
                colors: ['#99A9BF', '#F7BA2A', '#FF9900'],  // 等同于 { 2: '#99A9BF', 4: { value: '#F7BA2A', excluded: true }, 5: '#FF9900' }
                tableData: [],
                multipleSelection: [],
            };
        },

        mounted() {
            const that = this
            axios.get('http://39.107.32.92:80/api/gradeList',{
                params: {
                    'email': that.$route.query.email
                }
            }).then(
                response => {
                    console.log('请求成功了', response)
                    this.tableData = response.data
                },
                error => {
                    console.log('请求失败了', error.message)
                }
            )

        },

        methods: {
            change(row){
                var params = new URLSearchParams();
                params.append('taskName',row.taskName)
                params.append('volName', row.volName)
                params.append('phoneNum', row.phoneNum)
                params.append('isGrade', row.isGrade)
                axios({
                    method: "post",
                    url: "http://39.107.32.92:80/api/Graded",
                    params
                }).then(res => {
                    console.log(res)
                })
                console.log(row)
            },
            
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value;

            }
        },
    };
</script>

<style lang="scss" scoped>

</style>