<template>
    <div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            
            
            <el-form-item label="机构名称" prop="name">
                <el-input v-model="ruleForm.name" style="width: 50%;" placeholder="请输入机构名称"></el-input>
            </el-form-item>
            
            
            <el-form-item label="服务区域" prop="address">
                <el-select v-model="ruleForm.address" placeholder="请选择活动区域">
                    <el-option label="上海" value="上海"></el-option>
                    <el-option label="大连" value="大连"></el-option>
                </el-select>
                
            </el-form-item>

            <el-form-item label="详细地址" prop="particularAddress">
                <el-input v-model="ruleForm.particularAddress" style="width: 50%;display: -webkit-box;" placeholder="请输详细地址"></el-input>
            </el-form-item>
            
            <el-form-item
            style="width: 70%;"
                prop="email"
                label="机构邮箱"
                
            >
                <el-input v-model="ruleForm.email" placeholder="请输入机构邮箱"></el-input>
            </el-form-item>

            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="密码" prop="password">
                    <el-input type="password" v-model="ruleForm.password" placeholder="请输入登录密码" style="width: 30%;" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="checkPass">
                    <el-input type="password" v-model="ruleForm.checkPass" placeholder="请再次输入机构密码" style="width: 30%;" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>

            <el-form-item label="成为志愿者" prop="beVolunteer">
                <el-switch v-model="ruleForm.beVolunteer"></el-switch>
            </el-form-item>
            
            
            <el-form-item label="机构类型" prop="organizationType">
                <el-radio-group v-model="ruleForm.organizationType">
                    <el-radio label="政府机构"></el-radio>
                    <el-radio label="公益机构"></el-radio>
                    <el-radio label="民间机构"></el-radio>
                </el-radio-group>
            </el-form-item>
            
            
            <el-form-item label="其他说明" prop="other">
                <el-input type="textarea" v-model="ruleForm.other"></el-input>
            </el-form-item>
            
            
            <el-form-item>
                <el-button type="primary" @click="OrgRegister">立即注册</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'OrganizationRegister',

    data() {
         var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            } else {
                if (this.ruleForm.checkPass !== '') {
                    this.$refs.ruleForm.validateField('checkPass');
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.ruleForm.password) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        return {
            ruleForm: {
                email:'',
                name: '',
                address: '',
                particularAddress:'',
                organizationType: '',
                password: '',
                other: '',
                beVolunteer: 'false',
                checkPass:''
            },
            
            rules: {
                name: [
                    { required: true, message: '请输入机构名称', trigger: 'blur' },
                    { min: 5, max: 15, message: '长度在 5 到 15 个字符', trigger: 'blur' }
                ],
                address: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],
                
                other: [
                    { required: true, message: '请填写活动形式', trigger: 'blur' }
                ],
                password: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                checkPass: [
                    { validator: validatePass2, trigger: 'blur' }
                ],
                email:[
                        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                        { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
                ]
            
            },
        
        }
        
    },

    mounted() {
        console.log(this)
    },

    methods: {
         
         OrgRegister() {
            var params = new URLSearchParams();
            console.log(this)
            params.append('email', this._data.ruleForm.email)
            params.append('orgName', this._data.ruleForm.name)
            params.append('address', this._data.ruleForm.address)
            params.append('particularAddress', this._data.ruleForm.particularAddress)
            params.append('organizationType', this._data.ruleForm.organizationType)
            params.append('orgPassword', this._data.ruleForm.password)
            params.append('orgOther', this._data.ruleForm.other)
            params.append('beVolunteer', this._data.ruleForm.beVolunteer)
            
            axios({
                method: "post",
                url: "http://39.107.32.92:80/api/OrgRegister",
                params
            }).then(res => {
                 this.ruleForm = {
                    email: '',
                     name: '',
                     address: '',
                     particularAddress: '',
                     organizationType: '',
                     password: '',
                     other: '',
                     beVolunteer: 'false',
                     checkPass: ''
                }
                this.$message({
                    type: "success",
                    message: "注册成功",
                });

                console.log(res)
            })

        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        }
    
    },
};
</script>

<style lang="css" scoped>

</style>