<template>
    <div>
        <el-descriptions v-for="(item,i) in tableData" :key = "i" border direction="vertical" :column="3" style="margin-top: 20px;" >
            <el-descriptions-item label="任务名称">{{item.taskName}}</el-descriptions-item>
            <el-descriptions-item label="所属机构">{{item.orgName}}</el-descriptions-item>
            <el-descriptions-item label="开始时间" :span="2">{{item.startTime}}</el-descriptions-item>
            <el-descriptions-item label="机构邮箱" :span="1">{{item.orgEmail}}</el-descriptions-item>
            <el-descriptions-item label="任务地点" :span="2">{{item.particularAddress}}</el-descriptions-item>
            <el-descriptions-item label="其他描述">{{item.other}}</el-descriptions-item>
        </el-descriptions>
    </div>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'MyTask',

    data() {
        return {
            tableData:[]
        };
    },

    mounted() {
        const that = this
        axios.get('http://39.107.32.92:80/api/getMyTask', {
            params: {
                'phoneNum': that.$route.query.phoneNum
            }
        }).then(
            response => {
                console.log('请求成功了', response)
                this.tableData = response.data
            },
            error => {
                console.log('请求失败了', error.message)
            }
        ) 
    },

    methods: {
        
    },
};
</script>

