<template>
    <div>
        
        <div class="userfrom">
            <el-form ref="form" :model="form" label-width="80px">

                <el-form-item>
                    <el-upload class="avatar-uploader" 
                        name="file"
                        action="http://39.107.32.92:80/api/imgPost" 
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <div slot="tip" class="el-upload__tip" style="text-align: center;">请上传头像</div>
                        <img v-if="form.imageUrl" :src="form.imageUrl" class="avatar">
                    
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    
                    </el-upload>
                </el-form-item>

                
                <el-form-item label="真实姓名">
                    <el-input 
                    v-model="form.name" 
                    style="width: 25%;"
                    placeholder="请输入您的姓名"></el-input>
                </el-form-item>


                <el-form-item label="证件类型">
                    <el-select v-model="form.certificateType" placeholder="请选择证件类型" style="width: 25%;">
                        <el-option label="中华人民共和国居民身份证" value="IdCard"></el-option>
                        <el-option label="退伍军人证" value="veterenCard"></el-option>
                    </el-select>
                </el-form-item>


                <el-form-item label="证件号">
                    
                        <el-input v-model="form.certificateNum" placeholder="请输入您的证件号码" style="width: 40%;"></el-input>
                    
                </el-form-item>


                <el-form-item label="出生日期">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="选择日期" v-model="form.birthday" style="width: 100%;"></el-date-picker>
                    </el-col>
                </el-form-item>

                <el-form-item label="性别">
                    <el-radio-group v-model="form.sex" size="medium">
                        <el-radio label="男">
                            <i class="el-icon-male">男</i>
                        </el-radio>
                
                        <el-radio label="女">
                            <i class="el-icon-female">女</i>
                        </el-radio>
                
                    </el-radio-group>
                </el-form-item>
                
                
                <el-form-item label="文化水平">
                    <el-select v-model="form.educationalLevel" placeholder="请选择您的学历" style="width: 25%;">
                        <el-option label="博士" value="博士"></el-option>
                        <el-option label="硕士" value="硕士"></el-option>
                        <el-option label="本科" value="本科"></el-option>
                        <el-option label="高中" value="高中"></el-option>
                        <el-option label="初中" value="初中"></el-option>
                        <el-option label="小学" value="小学"></el-option>
                        
                    </el-select>
                </el-form-item>

                <el-form-item label="手机号码">
                    <el-input v-model="form.phoneNum" style="width: 35%;" placeholder="请输入您的手机号码"></el-input>
                </el-form-item>
                
                <el-form-item label="设置密码">
                    <el-input placeholder="请输入密码" v-model="form.passWord" style="width: 20%;" show-password></el-input>
                </el-form-item>

                <el-form-item label="确认密码">
                    <el-input placeholder="请再次输入密码" v-model="form.isPassword" style="width: 20%;" show-password></el-input>
                </el-form-item>

                <el-form-item label="现居地">
                    <el-input v-model="form.presentAddress" placeholder="请输入您的现居地" style="width: 40%;"></el-input>
                </el-form-item>

                <el-form-item label="自动疫情志愿">
                    <el-switch v-model="form.voluntarily"></el-switch>
                </el-form-item>

                <el-form-item label="掌握技能" prop="skill">
                    <el-checkbox-group v-model="form.skill">
                        <el-checkbox label="开车"></el-checkbox>
                        <el-checkbox label="计算机操作"></el-checkbox>
                        <el-checkbox label="手机操作"></el-checkbox>
                        <el-checkbox label="急救"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                
                <el-form-item label="自我介绍">
                    <el-input type="textarea" v-model="form.other"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="registered">立即注册</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'UserRegister',

    data() {
        return {
             form: {
                name: '',
                certificateType: '',
                certificateNum:'',
                birthday:'',
                sex:'',
                educationalLevel:'',
                phoneNum:'',
                passWord:'',
                skill: [],
                imageUrl: '',
                other:'',
                presentAddress:'',
                isPassword:'',
                voluntarily:false              
            },
            picUrl:''
        };
    },

    mounted() {
        
    },

    methods: {
        
        registered(){
            this.$options.methods.registere(this);
            this.$options.methods.conPhoneAndpic(this);

        },
        conPhoneAndpic(that){
            var params = new URLSearchParams();
            params.append('phoneNum', that._data.form.phoneNum)
            params.append('picUrl', that._data.picUrl)
            that.$axios({
                method: "post",
                url: "http://39.107.32.92:80/api/conPhoneNumAndUrl",
                crossDomain: true,
                params
            }).then(res => {
                console.log(res)
            })
        },
        registere(that) {
            var params = new URLSearchParams();
            params.append('volName',that._data.form.name)
            params.append('certificateType', that._data.form.certificateType)
            params.append('certificateNum', that._data.form.certificateNum)
            params.append('birthday', that._data.form.birthday)
            params.append('sex', that._data.form.sex)
            params.append('educationalLevel', that._data.form.educationalLevel)
            params.append('phoneNum', that._data.form.phoneNum)
            params.append('volPassword', that._data.form.passWord)
            params.append('skill', that._data.form.skill)
            params.append('other', that._data.form.other)
            params.append('voluntarily', that._data.form.voluntarily)
            params.append('presentAddress', that._data.form.presentAddress) 
            axios({
                method: "post",
                url: "http://39.107.32.92:80/api/VolRegister",
                crossDomain:true,
                params
            }).then(res => {
                that.form = {
                    name: '',
                    certificateType: '',
                    certificateNum: '',
                    birthday: '',
                    sex: '',
                    educationalLevel: '',
                    phoneNum: '',
                    passWord: '',
                    skill: [],
                    imageUrl: '',
                    other: '',
                    presentAddress: '',
                    isPassword: '',
                    voluntarily: false}
                    that.$message({
                        type: "success",
                        message: "注册成功",
                    });
                console.log(res)
            })
            
        },
        
        handleAvatarSuccess(res, file) {
            this.form.imageUrl = URL.createObjectURL(file.raw);
            this.picUrl = res
            console.log(res)
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
                this.$message.error('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },
      
    },
};
</script>

<style lang="css" scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .avatar-uploader{
      position: absolute;
      right: 10%;
      
  }
  .el-input__inner{
      width: 30%;
  }
  
</style>