<template>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="需求名称" style="width: 60%;" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>

        <el-form-item label="志愿类型" prop="taskType">
            <el-radio-group v-model="ruleForm.taskType">
                <el-radio label="疫情志愿服务"></el-radio>
                <el-radio label="扶贫志愿服务"></el-radio>
                <el-radio label="敬老志愿服务"></el-radio>
            </el-radio-group>
        </el-form-item>

        <el-form-item label="服务区域" prop="address">
            <el-select v-model="ruleForm.address" placeholder="请选择活动区域">
                <el-option label="河北廊坊" value="河北廊坊"></el-option>
                <el-option label="辽宁大连" value="辽宁大连"></el-option>
            </el-select>
        
        </el-form-item>
        
        <el-form-item label="详细地址" prop="particularAddress">
            <el-input v-model="ruleForm.particularAddress" style="width: 50%;display: -webkit-box;" placeholder="请输详细地址"></el-input>
        </el-form-item>


        <el-form-item label="活动时间" style="width: 70%;" required>
            <el-col :span="11">
                <el-form-item prop="startTime">
                    <el-date-picker 
                    type="date" 
                    value-format="yyyy-MM-dd"
                    placeholder="选择日期" 
                    v-model="ruleForm.startTime" 
                    format="yyyy 年 MM 月 dd 日"
                    style="width: 100%;">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col class="line" :span="2">——</el-col>
            <el-col :span="11">
                <el-form-item prop="endTime">
                    <el-date-picker 
                    type="date" 
                    v-model="ruleForm.endTime" 
                    placeholder="选择时间" 
                    format="yyyy 年 MM 月 dd 日"
                    value-format="yyyy-MM-dd" 
                    style="width: 100%;"></el-date-picker>
                </el-form-item>
            </el-col>
        </el-form-item>


        <el-form-item label="技能要求" prop="skill">
            <el-checkbox-group v-model="ruleForm.skill">
                <el-checkbox label="开车" name="skill"></el-checkbox>
                <el-checkbox label="计算机操作" name="skill"></el-checkbox>
                <el-checkbox label="手机操作" name="skill"></el-checkbox>
                <el-checkbox label="急救" name="skill"></el-checkbox>
            </el-checkbox-group>
        </el-form-item>

        <el-form-item label="学历要求" prop="educationLevel">
            <el-radio-group v-model="ruleForm.educationLevel">
                <el-radio label="大学及以上"></el-radio>
                <el-radio label="中学及以上"></el-radio>
                <el-radio label="小学"></el-radio>
            </el-radio-group>
        </el-form-item>

        <el-form-item label="人数要求" prop="personMaxNum">
            <el-input-number v-model="ruleForm.personMaxNum" :min="1" :max="1000" label="描述文字"></el-input-number>
        </el-form-item>
        
        <el-form-item label="详细说明" prop="other">
            <el-input type="textarea" v-model="ruleForm.other"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="Issue('ruleForm')">立即发布</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'IssueDemand',

    data() {
        return {
             ruleForm: {
                name: '',
                taskType:'',
                address:'',
                particularAddress:'',
                startTime:'',
                endTime:'',
                skill:[],
                educationLevel:'',
                personMaxNum:0,
                other:'', 
                email:''   
            },

            rules: {
                name: [
                    { required: true, message: '请输入活动名称', trigger: 'blur' },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                address: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],
                startTime: [
                    { required: true, message: '请选择日期', trigger: 'change' }
                ],
                endTime: [
                    {  required: true, message: '请选择时间', trigger: 'change' }
                ],
                taskType: [
                    {  required: true, message: '请至少选择一个活动性质', trigger: 'change' }
                ],
                particularAddress:[
                    { required: true, message: '请输入详细地址', trigger: 'blur'}
                ],
                other: [
                    { required: true, message: '请填写活动形式', trigger: 'blur' }
                ],
                
            }
        };
    },

    mounted() {
        this.ruleForm.email = this.$route.query.email
    },

    methods: {
        
        
        Issue() {
            var params = new URLSearchParams();
            params.append('taskName', this._data.ruleForm.name)
            params.append('taskType', this._data.ruleForm.taskType)
            params.append('taskAddress', this._data.ruleForm.address)
            params.append('particularAddress', this._data.ruleForm.particularAddress)
            params.append('startTime', this._data.ruleForm.startTime)
            params.append('endTime', this._data.ruleForm.endTime)
            params.append('skill', this._data.ruleForm.skill)
            params.append('educationLevel', this._data.ruleForm.educationLevel)
            params.append('personMaxnum', this._data.ruleForm.personMaxNum)
            params.append('other', this._data.ruleForm.other)
            params.append('orgEmail', this._data.ruleForm.email)
            console.log(this._data.ruleForm.startTime)
            axios({
                method: "post",
                url: "http://39.107.32.92:80/api/IssueTask",
                params
            }).then(res => {
                this.ruleForm = {
                    name: '',
                    taskType: '',
                    address: '',
                    particularAddress: '',
                    startTime: '',
                    endTime: '',
                    skill: [],
                    educationLevel: '',
                    personMaxNum: 0,
                    other: '',
                    email: '' 
                }
                this.$message({
                    type: "success",
                    message: "发布成功",
                });
                console.log(res)
            })

        },

        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
    },
};
</script>

<style lang="css" scoped>

</style>