<script>
    import LoginPage from '../loginpage/login_page'
export default {
    name: 'VolunteerPage',

    data() {
        return {
            isCollapse: false,
            flag: {
                isLogin: false,
                isVol: false,
            },
            data1: {}
        };
    },

    mounted() {
        this.$bus.$on('UserInformation', (data) => {
            this.data1 = data
        })
    },

    methods: {
         handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        logout() {
            this.flag.isLogin = false
            this.flag.isVol = false
        },
        Login(){
            this.$refs.loginPage.modelShow = true;
            this.$refs.loginPage.Identity = true
        },
        // 切换到志愿机构
        switchView(){
            this.$emit('showView');
        },
        // 登录后改变登录状态
        VolLogin(login, vol) {
            this.flag.isLogin = login
            this.flag.isVol = vol
            // this.flag.isOrg = org

        }
        
    },
    components:{
        LoginPage
    }
};
</script>

