// 该文件专门用于创建整个应用的路由器
// 引入VueRouter
import VueRouter from 'vue-router'

// 引入组件
import HomePage from '../page/home_page'
import UserRegister from '../page/user_register'
import OrganizationRegister from '../page/organization_register'
import VolunteerList from '../page/volunteer_list'
import RegisteredList from '../page/registered_list'
import OrgList from '../page/org_list'
import VolList from '../page/vol_list'
import RegisteredVolList from '../page/registered_vol_list'
import IssueDemand from '../page/issue_demand'
import MyTask from '../page/my_task'
import PublishedTask from '../page/published_task'
import DemandMaterials from '../page/demand_materials'
import MaterialsDonate from '../page/materials_donate'
import TaskList from '../page/task_list'
import MassagePage from '../massagepage/massagepage'
import ReadPage from '../massagepage/readpage'
import UnReadPage from '../massagepage/unreadpage'
import SendMsg from '../sendmsg/send_msg'
import WriteMsg from '../sendmsg/write_msg'
import MsgState from '../sendmsg/msg_state'

/* // 应用插件
Vue.use(VueRouter); */

// 创建并暴露一个路由器
export default new VueRouter({
    
    routes: [
        {
            path: '/',
            redirect: HomePage,
        },
        {
            name:'HomePage',
            path: '/homepage',
            component: HomePage
        },
        {
            name:'UserRegister',
            path:'/userregister',
            component:UserRegister
        },
        {
            name: 'OrganizationRegister',
            path: '/organizationregister',
            component: OrganizationRegister
        },
        {
            name: 'VolList',
            path: '/vollist',
            component: VolList,
            children: [
                {
                    name: 'VolunteerList',
                    path: '/volunteerlist',
                    component: VolunteerList,
                },
                {
                    name: 'RegisteredVolList',
                    path: '/registeredvollist',
                    component: RegisteredVolList,
                },
                
                {
                    name: 'PublishedTask',
                    path: '/publishedtask',
                    component: PublishedTask
                }
            ]
        },
        {
            name:'OrgList',
            path:'/orglist',
            component:OrgList,
            children:[
                {
                    name: 'TaskList',
                    path: '/tasklist',
                    component: TaskList,
                },
                {
                    name: 'RegisteredList',
                    path: '/registeredlist',
                    component: RegisteredList,
                }
            ]
        },
        {
            name:'IssueDemand',
            path:'/issuedemand',
            component:IssueDemand
        },
        {
            name:'MyTask',
            path:'/mytask',
            component:MyTask
        },
        {
            name: 'DemandMaterials',
            path: '/demandmaterials',
            component: DemandMaterials
        },
        {
            name: 'MaterialsDonate',
            path: '/materialsdonate',
            component: MaterialsDonate
        },
        {
            name: 'MassagePage',
            path: '/massagepage',
            component: MassagePage,
            children: [
                {
                    name: 'ReadPage',
                    path: '/readpage',
                    component: ReadPage,
                },
                {
                    name: 'UnReadPage',
                    path: '/unreadpage',
                    component: UnReadPage,
                }
            ]
        },
        {
            name: 'SendMsg',
            path: '/sendmsg',
            component: SendMsg,
            children: [
                {
                    name: 'WriteMsg',
                    path: '/writemsg',
                    component: WriteMsg,
                },
                {
                    name: 'MsgState',
                    path: '/msgstate',
                    component: MsgState,
                }
            ]
        }
       
    ]
});
