<template>
    <div >
        <template>
            
            <el-descriptions v-for="(item,i) in tableData" :key = "i" class="margin-top" style="margin-top: 5px;" :column="3" :size="size" border>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-user"></i>
                        任务名称
                    </template>
                    {{item.taskName}}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-mobile-phone"></i>
                        开始时间
                    </template>
                    {{item.startTime}}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-location-outline"></i>
                        现有人数
                    </template>
                    {{item.personNum}}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-tickets"></i>
                        备注
                    </template>
                    {{item.other}}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-office-building"></i>
                        详细地址
                    </template>
                    {{item.particularAddress}}
                </el-descriptions-item>
            </el-descriptions>
        
        </template>
    </div>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'PublishedTask',

    data() {
        return {
            tableData:[],
            size: ''
        };
    },

    mounted() {
        const that = this
        axios.get('http://39.107.32.92:80/api/SelectOrgEntriedTask', {
            params: {
                'email': that.$route.query.email
            }
        }).then(
            response => {
                console.log('请求成功了', response)
                this.tableData = response.data
            },
            error => {
                console.log('请求失败了', error.message)
            }
        ) 
    },

    methods: {
        
    },
};
</script>

<style lang="css" scoped>

</style>