<template>
    <div>

        <el-button type="primary" icon="el-icon-edit" @click="writeMsg">写消息</el-button>

        <WriteMsg ref="WriteMsgDilog"></WriteMsg>
        <MsgState ref="StateDilog"></MsgState>

        <el-card class="box-card" shadow="hover" v-for="(msg,i) in dataMsg" :key="i" style="margin-top: 20px;">
            <div slot="header" class="clearfix">
                <span>
                    <h1>发信方：{{msg.taskName}}</h1>
                </span>
                <span style="float: right; padding: 3px 0">{{msg.sendTime}}</span>
                <!-- <el-button style="float: right; padding: 3px 0" type="text">删除</el-button> -->
            </div>
        
            <div style="padding: 0 50px;">
                <span>
                    {{msg.content}}
                </span>
            </div>
            <div class="bottom clearfix">
                
                <el-button type="primary" class="button" @click="stateMsg(msg.messageNo)">查看消息状态</el-button>
                <el-button type="primary" class="button" @click="deleteMsg(msg.messageNo,msg.i)">删除</el-button>
            </div>
        </el-card>
    </div>
</template>

<script>
    import WriteMsg from './write_msg'
    import MsgState from './msg_state'
export default {
    name: 'SendMsg',

    data() {
        return {
            email:'',
            TaskNameAndNo:[],
            dataMsg:[],
            msgs: [
                {
                    name: '琪琪',
                    content: '明天集合'
                },
                {
                    name: '老公',
                    content: '明天睡觉'
                },
                {
                    name: '老公',
                    content: '明天睡觉'
                },
                {
                    name: '老公',
                    content: '明天睡觉'
                },
                {
                    name: '老公',
                    content: '明天睡觉'
                }
            ]
        };
    },

    mounted() {
        this.email = this.$route.query.data1.email
        console.log(this.$route.query.data1.email)

        const that = this
        that.$axios.get('http://39.107.32.92:80/api/findSendMsg', {
            params: {
                'email': that.$route.query.data1.email,
            }
        }).then(
            response => {
                console.log('请求成功了', response)
                this.dataMsg = response.data
            },
            error => {
                console.log('请求失败了', error.message)
            }
        )

        that.$axios.get('http://39.107.32.92:80/api/findAllTaskNameAndNo', {
            params: {
                'email': that.$route.query.data1.email,
            }
        }).then(
            response => {
                console.log('请求成功了', response)
                this.TaskNameAndNo = response.data
            },
            error => {
                console.log('请求失败了', error.message)
            }
        )
    },

    methods: {
        writeMsg(email){
            this.$refs.WriteMsgDilog.dataform.orgEmail = this.email
            this.$refs.WriteMsgDilog.TaskNameAndNo = this.TaskNameAndNo
            this.$refs.WriteMsgDilog.dialogFormVisible = true
        },
        deleteMsg(messageNo,i){
            this.$axios.get('http://39.107.32.92:80/api/deleteMsg', {
                params: {
                    'messageNo': messageNo,
                }
            }).then(
                response => {
                    console.log('请求成功了', response)
                },
                error => {
                    console.log('请求失败了', error.message)
                }
            )
            this.dataMsg.splice(i, 1)
        },
        stateMsg(messageNo){
             this.$axios.get('http://39.107.32.92:80/api/findMsgState', {
                params: {
                    'messageNo': messageNo,
                }
            }).then(
                response => {
                    console.log('请求成功了', response)
                    this.$refs.StateDilog.dialogTableVisible = true
                    this.$refs.StateDilog.stateTable = response.data
                },
                error => {
                    console.log('请求失败了', error.message)
                }
            )
        }
    },
    components:{
        WriteMsg,
        MsgState
    }
};
</script>

<style lang="css" scoped>
    .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 80vw;
    line-height: 10px;
  }
   .bottom {
       text-align: center;
    margin-top: 13px;
    line-height: 12px;
  }
  .mass{
      width: 70%;
      
  }
</style>